.basic-layout-wrapper{
    /* background: url('./img/Bg.jpg') no-repeat; */
    background-repeat: no-repeat;
    background-size:cover;
    height: 1080px;
    width: 1920px;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 50%;
    right: 0px;
    bottom: 0px;
    overflow: hidden;
}